* {
    box-sizing: border-box;
    margin: 0;
}

html,
body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $main-font, $font-sans-serif;
    font-size: var(--root-font-size);
    line-height: $font-line-height;
    color: var(--c-text);
    background: var(--c-bg);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $header-font, $font-sans-serif;
    line-height: 1;
}

p,
li {
    font-size: var(--f-large);
    max-width: 80ch;
}

a {
    display: inline-block;
    position: relative;
    color: inherit;
    text-decoration: none;

    &::before {
        content: "";
        position: absolute;
        bottom: rem(1);
        height: rem(1);
        width: 100%;
        left: 0;
        border-bottom: rem(1) var(--c-text) solid;
        transform: scaleX(1);
        transition: transform .2s linear;
    }

    &:hover,
    &:focus {
        &::before {
            transform: scaleX(0);
        }
    }
}

ul {
    margin: 0;
    padding: 0;
    list-style-position: inside;
}

img {
    max-width: 100%;
    height: auto;
}